<template>
    <div>
          <transition name="slide-fade">
        <b-form v-if="showCEP" class="form-address-cep">
            <div class="col-sm-12">
                <the-mask mask="#####-###" class="mb-1 form-control-lg form-control" type="text" placeholder="Digite seu CEP" v-model="address.cep"></the-mask>
            </div>
            <b-button variant="theme" size="lg" class="button-search mt-1 mb-1" @click="searchAddress()">Buscar</b-button>
            <b-form-text class="mt-2">
                <b-link @click="invertSearch()">
                    <span>Não sabe o CEP? Digite seu endereço</span>
                </b-link>
            </b-form-text>
        </b-form>
        </transition>

        <transition name="animation">
        <b-form v-if="showAddress" class="form-address">
            <h3 class="search-address-title">
                            <strong>Informe o endereço de entrega!</strong>
                        </h3>
                    <div class="form-group row">
                    <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
                        <b-select v-model="address.uf" :options="listStates" size="lg" :class="[{ disabled: disableFields }]" :disabled="disableFields"></b-select>
                    </div>
                    </div>
                
                <div class="form-group row">
                    <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
                        <b-select v-model="address.city" :options="listCities(address.uf)" size="lg" :class="[{ disabled: disableFields }]" :disabled="disableFields"></b-select>
                    </div>
                </div>
                
                <div class="form-group row">
                    <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
                        <b-form-input type="text" placeholder="Digite seu endereço" size="lg" v-model="address.street" :class="[{ disabled: disableFields }]" :disabled="disableFields"></b-form-input>
                    </div>
                </div>

                <div class="form-group row">
                    <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
                        <b-form-input type="text" placeholder="Nº" v-model="address.number" size="lg"></b-form-input>
                    </div>
                </div>
                
                <b-btn class="button-search2 mb-2" variant="theme" size="lg" @click="setAddress()">Buscar</b-btn>
                <b-btn class="button-back" variant="dark" size="lg" @click="invertSearch()">Voltar</b-btn>
        </b-form>
        </transition>
    </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import {TheMask} from 'vue-the-mask'
export default {
  data () {
    return {
      disableFields: true,
      showCEP: true,
      showAddress: false,
      address: {
        cep: '',
        uf: 'MG',
        city: {name: 'Uberlândia', uf: 'MG'},
        street: '',
        number: ''
      },
      ...mapState(['lastUsed'])
    }
  },
  computed: {
    ...mapGetters({
      listStates: 'allStates',
      listCities: 'allCitiesByUF'
    //   address: this.lastUsed
    })
  },
  methods: {
    invertFields () {
      this.disableFields = !this.disableFields
    },
    invertSearch () {
      this.invertFields()
      this.showCEP = !this.showCEP
      this.showAddress = !this.showAddress
    },
    async searchAddress () {
      let _axios = this.$http.create()
      let cep = this.address.cep
      let response = await _axios.get(
        'correios/cep/' + cep
      )
      let _address = response.data.data
      this.address = {
        cep: _address.cep,
        uf: _address.uf,
        city: { name: _address.localidade, uf: _address.uf },
        street: _address.logradouro,
        number: ''
      }
      this.invertSearch()
    },
    setAddress () {}
  },
  created () {},
  components: {TheMask}
}
</script>

<style scoped>
.form-address {
  width: 100%;
  max-width: 800px;
  margin: 0 auto 60px;
}

.form-address-cep {
  width: 100%;
  max-width: 370px;
  margin: 30px auto 60px;
}

.search-address .disabled {
    background: #cecece;
    -webkit-appearance: none;
    opacity: 1;
}

.search-address-title {
    font-family: "Give You Glory", cursive;
    color: #fff;
    padding-top: 4%;
}

.animation-enter-active {
  animation: bounce-out-right 0.5s reverse;
}
.animation-leave-active {
  animation: bounce-out-right 0.5s;
}
@keyframes bounce-out-right {
  20% {
    opacity: 1;
    transform: translate3d(-20px, 0, 0);
  }
  to {
    opacity: 0;
    transform: translate3d(600px, 0, 0);
  }
}

.slide-fade-enter-active {
  transition: all .4s ease;
}
.slide-fade-leave-active {
  transition: all .2s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active em versões anteriores a 2.1.8 */ {
  transform: translateX(10px);
  opacity: 0;
}

.button-search {
  border: 0 !important;
	color: white;
	height: 50px;
	line-height: 20px;
	border-radius: 8px;
	box-sizing: border-box;
	font-size: 1.3em;
	font-weight: normal;
	padding-bottom: 12px;
	padding-top: 12px;
	width: 92%;
	background-color: #ffb42d;
}

.button-search2 {
  border: 0 !important;
	color: white;
	height: 50px;
	line-height: 20px;
	border-radius: 8px;
	box-sizing: border-box;
	font-size: 1.3em;
	font-weight: normal;
	padding-bottom: 12px;
	padding-top: 12px;
	width: 100%;
	background-color: #ffb42d;
}

.button-back {
  border: 0 !important;
	color: white;
	height: 50px;
	line-height: 20px;
	border-radius: 8px;
	box-sizing: border-box;
	font-size: 1.3em;
	font-weight: normal;
	padding-bottom: 12px;
	padding-top: 12px;
	width: 100%;
  background-color: #343a40;
}
</style>



